export default {

	data() {
		return {
			ratio:'',
			team:'',
			teamAchievement:'',
			level:'',
			invite:'',
			inviteList:[]
		};
	},
	onLoad() {
		if (!uni.getStorageSync("token") && !uni.getStorageSync("SecretKey")) {
			this.$base1._isLogin()
		}
		this.initData()

	},
	onReady() {
	},
	onPullDownRefresh() {
		this.initData()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		initData() {
			//我的团队上面蓝色框框的数据
			uni.request({
				url: this.baseUrl + "/member-invite",
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						this.team = res.data.data.Team
						this.teamAchievement = res.data.data.TeamAchievement
						this.ratio = res.data.data.Ratio
						this.level = res.data.data.Level
						this.invite = res.data.data.Invite
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
			//直推列表
			
			uni.request({
				url: this.baseUrl + "/invite-list",
				data:{
					page:1,
					count:100000
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					 if (res.data.status == 1) {
						this.inviteList = res.data.data.list
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		jumpToMyTeam2(){
			uni.navigateTo({
				url:"./my-team2"
			})
		}
	}
}
